<template>
	<div class="background" v-if="sellerdetail">
	<!-- 顶部商家信息 -->
		<div class="seller_title">
			<h1>{{sellers.name}}</h1>
				<!-- 引入星星组件 -->
				<div class="stars_wrap">
					<Stars></Stars>	
				</div>
		</div>
		<!-- 优惠信息 -->
		<div class="seller_discount">
			<!-- 分割-->
			<div class="split">
				<div class="line"><hr></div>
				<div class="info">优惠信息</div>
				<div class="line"><hr></div>
			</div>
			<!-- 內容-->
			<div>
				<div class="seller_discount_content" v-for="(v,k) in sellers.supports">
					<div class="seller_discount_content_img">
						<img src="../../../static/images/decrease.png" alt="" v-if="v.type==0">
						<img src="../../../static/images/discount.png" alt="" v-if="v.type==1">
						<img src="../../../static/images/special.png" alt="" v-if="v.type==2">
						<img src="../../../static/images/invoice.png" alt="" v-if="v.type==3">
						<img src="../../../static/images/guarantee.png" alt="" v-if="v.type==4">
					</div>
					<div class="seller_discount_content_txt"><span>{{v.description}}</span></div>
				</div>
			</div>
		</div>
		<!-- 商家公告 -->
		<div class="seller_notice">
			<!-- 分割-->
			<div class="split">
				<div class="line"><hr></div>
				<div class="info">商家公告</div>
				<div class="line"><hr></div>
			</div>
			<!-- 內容-->
			<div class="seller_notice_content">
				<p>{{sellers.bulletin}}</p>
			</div>
		</div>
		<i class="iconfont icon-X"  @click="closeSellerDetail"></i>
	
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import Stars from "../../components/stars/stars.vue"
	export default{
		computed:{
			...mapGetters([
				"sellerdetail",
				"sellers",
			]),

		},
		components:{
			Stars
		},
		methods:{
			closeSellerDetail(){
				this.$store.dispatch("closeSellerDetail")
			}
		}
	}
</script>
<style lang="less" scoped>
	.background{
		z-index:100;
		position: fixed;
		background:rgba(0,0,22,0.8);
		top:0;
		bottom: 0;
		left:0;
		right:0;
		text-align:center;
		padding:1.4rem 0.7rem 0.8rem 0.7rem;
		color:white;
		.seller_title{
			h1{
				font-size:0.4rem;
			}
			.stars_wrap{
				width:5rem;
				margin: 0.5rem auto;
				& img{
					// width: 0.4rem;
				}
			}
		}
		.split{
			display:flex;
			.line{
				flex-grow:2;
				hr{
					border:none;
					border-top:0.01rem solid gray;
				}
				color:gray;
			}
			.info{
				font-size:0.3rem;
				padding:0 0.2rem;
			}
		}
		.seller_discount_content{
			display:flex;
			width:5rem;
			margin:0.3rem auto;
			text-align:left;
			color: rgba(255,255,255,0.8);
			.seller_discount_content_img{
				flex-grow:1;
				margin-right:0.2rem;
				img{
					width:0.4rem;
				}
			}
			.seller_discount_content_txt{
				flex-grow:8;
				width:6rem;
				text-align:left;
				line-height:0.4rem;
				font-size:0.27rem;
			}
		}
		.seller_notice_content{
			text-align:left;
			width:5.4rem;
			font-size:0.3rem;
			font-family: "华文细黑";
			color: rgba(255,255,255,0.8);
			line-height:0.6rem;
			margin:0.3rem auto;
		}
	}
</style>


