<template>
	<div class="pm">
		<div @click.stop="minus">
	<transition name="slide-fade">
			<i class="iconfont icon-jianhao" v-show="food.count"></i>
	</transition>
		</div>
		<div><p v-show="food.count">{{food.count}}</p></div>
		<div @click.stop="plus">
			<i class="iconfont icon-jiahao"></i>
		</div>
	</div>
</template>
<script>
	
	export default{
		props:["food"],
		methods:{
			plus(){
				if(!this.food.count){
					this.$set(this.food,"count",1);		
					return;
				}
				this.food.count++;
			},
			minus(){
				if(!this.food.count){
					return;
				}
				this.food.count--;

			}
		}
	}
</script>
<style lang="less" scoped>
	.pm{
		padding:0 0.3rem;
		display: flex;
		justify-content:space-between;
		color: rgb(0,160,220);
		line-height: 0.3rem;
		font-size: 0.3rem;
		p{
			color:gray;
		}
	}
	.slide-fade-enter-active {
 		transition: all .3s ease;
	}
	.slide-fade-leave-active {
	  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	.slide-fade-enter, .slide-fade-leave-to{
	  opacity: 0;
	}
</style>


