<template>
	<div>
		<div class="header_wrap">
			<!-- 这是模糊的背景 -->
			<img :src="sellers.avatar" alt="" class="background">
			<!-- 这是header部分 -->
			<div  class="header">
				<!-- 左侧图片 -->
				<div class="left">
					<img :src="sellers.avatar" alt="" class="avatar">
				</div>
				<!-- 右侧文字 -->
				<div class="right">
					<div class="header_top">
						<img src="../../../static/images/brand.png" class="brand" alt="">
						<p class="title">{{sellers.name}}</p>
					</div>
					<div>
						<p class="header_text">{{sellers.description}}</p>
						</div>
					<div class="header_bottom">
						<div class="header_bottom-left">
							<img src="../../../static/images/decrease.png" alt="" class="decrease">
							<p class="header_text decrease_info" v-if="sellers.supports">
								<span>{{sellers.supports[0].description}}</span>
							</p>
						</div>
						<div class="decrease_text" @click="showSellerDetail"><span class="header_text " v-if="sellers.supports">{{sellers.supports.length}}个></span></div>
					</div>
				</div>
			</div>
			<div class="bulletin">
				<div class="bulletin_left">
					<img src="../../../static/images/bulletin.png" class="bulletin_img"alt="">
				</div>
				<div class="bulletin_middle">
					{{sellers.bulletin}}
				</div>
				<!-- 公告 -->
				<div class="bulletin_right">
					<span>></span>
				</div>
			</div>
		</div>
		<!--商家详细信息-->
		<transition enter-active-class="animated  zoomIn" leave-active-class="animated fadeOutUp">
			<sellerdetail v-if="sellerdetail"></sellerdetail>
		</transition>

	</div>
</template>
<script>
	import {mapGetters} from "Vuex"
	import sellerdetail from "../sellerdetail/sellerdetail.vue"

	export default{
		computed:{
			...mapGetters([
				"sellers",
				"sellerdetail"
			])
		},
		components:{
			sellerdetail
		},
		methods:{
			showSellerDetail(){
				this.$store.dispatch("showSellerDetail")
			}
		},
		created(){
			this.$store.dispatch("getSellers")
		}
	}
</script>
<style lang="less" scoped>
	.header_wrap{
		position: relative;
		overflow: hidden;
		.background{
			position: absolute;
			top:0;
			bottom:0;
			left:0;
			right:0;
			width: 100%;
			height: 100%;
			z-index: -10;
			filter:blur(6px)
		}
		.header{
			height: 1.7rem;
			background:rgba(7,17,27,0.5);
			blur:10px;
			padding-top: 0.48rem;
			padding-left: 0.48rem;
			display: flex;
			.left{
				margin-right: 0.3rem;
				.avatar{
					width:1.28rem;
					height: 1.28rem;
					border-radius: 0.04rem;
				}
			}
			.right{
				width:5rem;
				.header_text{
					font-size: 0.25rem;
					color: rgba(255,255,255,0.8);
					line-height: 0.36rem;
					font-family: "华文细黑";
				}
				.header_top{
					display: flex;
					margin-bottom: 0.08rem;
					.title{
						font-size: 0.32rem;
						color: rgb(255,255,255);
						font-weight: bold;
						line-height: 0.36rem;
					}
					.brand{
						width: 0.6rem;
						height:0.3rem;
						margin-right: 0.2rem
					}
				}
				.header_bottom{
					display: flex;
					justify-content:space-between;
					margin-top: 0.1rem;
					.header_bottom-left{
						display: flex;
					}
					.decrease{
						width: 0.4rem;
						height:0.4rem;
						margin-right: 0.1rem;
					}
					.decrease_info{
						width:3.3rem;
						overflow: hidden;
						white-space: nowrap;
						line-height: 0.4rem;
					}
					.decrease_text{
						text-align: center;
						line-height: 0.5rem;
						width: 1rem;
						height: 0.5rem;
						background:rgba(0,0,0,0.2);
						border-radius: 0.25rem
					}
				}
			}
		}
		.bulletin{
			display: flex;
			height: 0.6rem;
			background:rgba(7,17,27,0.6);
			line-height: 0.6rem;
			color: rgba(255,255,255,0.8);
			// font-family: "华文细黑";
			.bulletin_left{
				padding: 0.06rem 0.2rem 0 0.3rem;
				.bulletin_img{
					width: 0.5rem;
				}
			}
			.bulletin_middle{
				overflow: hidden;
				text-overflow :ellipsis;
				white-space: nowrap;
			}
			.bulletin_right{
				padding-right: 0.2rem; 
			}

		}
	}
	.v-enter-active,.v-leave-active{
		transition:all 3s ease;
	}
	.v-enter-active{
		opacity:1;
	}
	.v-leave-active{
		opacity:0;
	}
	
</style>


