<template>
	<div>
	   <!-- 主体 -->
        <div class="content">
            <div class="goods_menu" ref="main_left">
                <div>
                   <div v-for="(v,k) in goods" class="goods_menu_child" :class="k==index?'goods_menu_selected':'goods_menu_nomal'" @click.stop="scrollRight(k,$event)">
                        <span>{{v.name}}</span>
                   </div>
                </div>
            </div>
            <div class="goods_foods" ref="main_right">
                <div>
                    <div v-for="(v,k) in goods">
                        <div class="goods_foods_title">
                            <div class="goods_foods_title_left"></div>
                            <div class="goods_foods_title_right">{{v.name}}</div>

                        </div>
                        <!-- {{v.foods.length}} -->
                        <div v-for="(v1,k1) in v.foods" class="goods_foods_details" >
                            <div class="goods_foods_details_img">
                                <img :src="v1.image" height="36" width="60" alt="">
                            </div>
                            <div class="goods_foods_details_txt" @click="show_food_detail(k,k1)">
                                <div class="goods_foods_details_name" ><p>{{v1.name}}</p></div>
                                <div class="goods_foods_details_description" v-if="v1.description"><p>{{v1.description}}</p></div>
                                <div class="goods_foods_details_sellCount">
                                    <p>月售{{v1.sellCount}}份&nbsp;&nbsp;&nbsp;&nbsp;好评率{{v1.rating}}%</p>
                                </div>
                                <div class="goods_foods_details_last">
                                    <div>
                                        <span class="goods_foods_details_price">￥{{v1.price}}</span>&nbsp;&nbsp;
                                        <s v-show="v1.oldPrice"class="goods_foods_details_oldPrice" >￥{{v1.oldPrice}}</s>
                                    </div>
                                    <div class="pm">
                                        <plusminus :food="v1"></plusminus>
                                        <!-- <plusminus :index="k" :index1="k1"></plusminus> -->
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <transition enter-active-class="animated  zoomIn" leave-active-class="animated fadeOutLeft">
                 <fooddetail :good="show_food_k" :good1="show_food_k1" v-if="show_food_k1!==-1" @close="close_food_detail"></fooddetail> 
            </transition>
        </div>

	</div>

</template>
<script>
   
    import fooddetail from "../fooddetail/fooddetail.vue"
    import plusminus from "../plusAndMinus/plusminus.vue"
    import {mapGetters} from "vuex"


    export default{
        data(){
            return{
                arr_foods:[],
                index:0,
                show_food_k:-1,
                show_food_k1:-1
            }
        },
    	computed:{
    		...mapGetters([
    			"goods"
    		])
    	},
        components:{
            fooddetail,
            plusminus
        },
    	created(){
    		this.$store.dispatch("getGoods");
            this.$nextTick(()=>{
               setTimeout(this._initBS,20) 
            })
            setTimeout(this.acountRight,20) 
            setTimeout(this.scrollLeft,20) 

        
    	},
        methods:{
            _initBS(){
                // 左侧菜单
                this.main_left=new BScroll(this.$refs.main_left,{
                    click:true,
                    probeType: 3
                })
                // 右侧菜单
                this.main_right=new BScroll(this.$refs.main_right,{
                    click:true,
                    probeType: 3
                })
           
            // 获取右侧每个类目的高度，放进一个数组arr_foods,created触发
                var height=0;
                for(var i in this.goods){
                    height+=this.goods[i].foods.length*110.39+33.11;
                    this.arr_foods.push(height);
                }
                // console.log(this.arr_foods);
                // 绑定滚动事件，获取坐标
                this.main_right.on('scroll', (pos) => {
                    var y=-pos.y;
                    for(var i in this.arr_foods){
                         if(y<this.arr_foods[i]){
                            this.index=i;
                            break;
                         }
                    }
                })
            },
            // 点击左侧显示右侧相关数据
            scrollRight(index,e){
                // console.log(index+"-------",this.index);
                if ( !e._constructed ){
                    return;
                }
                this.index=index;
                if(this.index==0){
                     this.main_right.scrollTo(0, 0,300);
                }
                this.main_right.scrollTo(0, -this.arr_foods[index-1],200)
                // console.log(index)
            },
            show_food_detail(k,k1){
                this.show_food_k=k;
                this.show_food_k1=k1;
                // console.log(this.show_food_k1);
            },
            close_food_detail(){
                this.show_food_k1=-1;
            }
        }
    }
</script>

<style lang="less" scoped>
    .content{
        display: flex;
        overflow:hidden;
        position: absolute;
        top:3.6rem;
        bottom:0.92rem;
        .goods_menu{
            // width: 1rem;
            flex:1;
            // background: #f3f5f7;
            .goods_menu_selected{
                background: white;
            }
            .goods_menu_nomal{
                background: #f3f5f7;
            }
            .goods_menu_child{
                display: table;
                height:1.2rem;
                width:100%;
                text-align: center;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                font-size: 0.24rem;
                font-weight: 200;
                &>span{
                   vertical-align: middle; 
                   display: table-cell;
                   line-height: 0.35rem;
                }
            }
        }
        .goods_foods{
            flex:3;
            overflow:hidden;
            .goods_foods_title{
                background: #f3f5f7;
                height:0.6rem;
                font-size: 0.25rem;
                line-height: 0.6rem;
                color:gray;
                display:flex;
                .goods_foods_title_left{
                    width: 0.08rem;
                    height: 100%;
                    background: lightgray;
                    margin-right: 0.1rem;
                }
            }
            .goods_foods_details{
                   height: 1.2rem;
                    padding: 0.4rem 0;
                    display: flex;
                    border-bottom: 0.01px solid #f3f5f7;;
                    margin: 0 0.4rem;
                .goods_foods_details_img{
                    flex:1;
                    margin-right: 0.2rem;
                    img{
                        width:100%;
                        height: 1.15rem;
                    }
                    
                }
                .goods_foods_details_txt{
                     flex:3;
                     .goods_foods_details_name{
                          font-size: 0.25rem;
                          margin-bottom: 0.15rem;
                     }
                     .goods_foods_details_description,.goods_foods_details_sellCount{
                          font-size: 0.15rem;
                          margin-bottom: 0.15rem;
                          color:gray;
                          & p{
                            overflow: hidden;
                            white-space: nowrap;
                            height: 11px;
                            text-overflow: ellipsis;
                            width: 3.3rem;
                              
                          }
                     }
                     .goods_foods_details_last{
                         display: flex;
                         & > div{
                            flex:1;
                         }
                         .goods_foods_details_price{
                              font-size: 0.25rem;
                              color:red;
                              font-weight: bolder;
                         }
                         .goods_foods_details_oldPrice{
                              font-size: 0.15rem;
                              color:gray;
                         }
                        
                     }
                }
                .pm{
                    height: 0.5rem;
                    line-height: 0.5rem;
                }
            }
        }
    }
</style>



