import Vue from "vue"
import Vuex from "vuex"


import actions from "./actions.js"

import goods from "./modules/goods.js"
import sellers from "./modules/sellers.js"
import ratings from "./modules/ratings.js"


Vue.use(Vuex)

export default new Vuex.Store({
	actions,
	modules:{
		goods,
		sellers,
		ratings
	}
})


