import types from "../types.js"
import axios from "axios"


const state={
	ratings:[]
}
const getters={
	ratings(state){
		return state.ratings
	}
}
const actions={
	getRatings({commit}){
		axios.get("/shop/static/data.json").then(resp=>{
			if(resp.data.errno===0){
				// console.log(resp.data.data)
				commit(types.GET_RATINGS,resp.data.ratings)
			}
		})
	}
}
const mutations={
	[types.GET_RATINGS](state,ratings){
		state.ratings=ratings
	}
}

export default{
	state,
	getters,
	mutations,
	actions
}


