import types from "../types.js"
import axios from "axios"


const state={
	goods:[],
}
const getters={
	goods(state){
		return state.goods;
	},
}
const actions={
	getGoods({commit}){
		axios.get("/shop/static/data.json").then((resp)=>{
			if(resp.data.errno===0){
				commit(types.GET_GOODS,resp.data.goods)
			}
			// console.log(resp.data.data)
		})
	},
}
const mutations={
	[types.GET_GOODS](state,goods){
		state.goods=goods;
		// console.log(goods)
	},
}

export default{
	state,
	getters,
	mutations,
	actions

}


