<template>
	<div class="all" ref ='seller'>
        <div>
            <!-- 商家信息头部 -->
    		<div class="header">
                <div class="header_top">
                    <div class="header_top_left">
                        <p class="header_top_left_title">{{sellers.name}}</p>
                        <div class="header_top_left_detail">
                            <div class="header_top_left_stars">
                                <Stars></Stars>
                            </div>
                            <div class="header_top_left_num">
                                <span>&nbsp;&nbsp;(661)</span>
                            </div>
                            <div class="header_top_left_sellCount">
                                <span>月售{{sellers.sellCount}}单</span>
                            </div>
                            
                        </div>

                    </div>
                    <div class="header_top_right">
                        <div  class="iconfont_like">
                             <i class="iconfont icon-xin1" :class="{'like':status,'unlike':!status}" @click="changeStatus($event)"></i>
                        </div>
                        <div>
                            <span>{{status?"已收藏":"收藏"}}</span>
                        </div>
                    </div>
                </div> 
                <div  class="header_bottom">
                    <div  class="header_bottom_left">
                        <div><p class="header_bottom_info">起送价</p></div>
                        <div><span class="header_bottom_amount">{{sellers.minPrice}}</span>元</div>
                    </div>
                    <div  class="header_bottom_middle">
                        <div><p class="header_bottom_info">商家配送</p></div>
                        <div><span class="header_bottom_amount">{{sellers.deliveryPrice}}</span>元</div>
                    </div>
                    <div  class="header_bottom_right">
                        <div><p class="header_bottom_info">平均配送时间</p></div>
                        <div><span class="header_bottom_amount">{{sellers.deliveryTime}}</span>分钟</div>
                    </div>
                </div>        
            </div>
            <!-- 分隔div -->
            <div class="split"></div>
            <!-- 公告与活动 -->
            <div class="adv">
                <div class="adv_title">公告与活动</div>
                <div class="adv_content"><span>{{sellers.bulletin}}</span></div>
                <div class="adv_detail" v-for="v in sellers.supports">
                    <div class="adv_detail_img">
                        <img src="../../../static/images/decrease.png" alt="" v-if="v.type==0">
                        <img src="../../../static/images/discount.png" alt="" v-if="v.type==1">
                        <img src="../../../static/images/special.png" alt="" v-if="v.type==2">
                        <img src="../../../static/images/invoice.png" alt="" v-if="v.type==3">
                        <img src="../../../static/images/guarantee.png" alt="" v-if="v.type==4">
                    </div>
                    <div class="adv_detail_txt"><p>{{v.description}}</p></div>
                </div>
            </div>
            <!-- 分隔div -->
            <div class="split"></div>
            <!-- 商家实景 -->
            <div class="view">
                  <div class="view_title">商家实景</div>
                  <div  class="view_pics" ref="view_pics">
                    <div ref = 'picWidth'>
                        <img :src="v"  alt="" v-for="v in sellers.pics" ref = 'picWidth_child'>
                    </div>
                  </div>
            </div>
            <!-- 分隔div -->
            <div class="split"></div>
            <!-- 商家信息 -->
            <div class="adv">
                <div class="adv_title">商家信息</div>
                <div class="adv_detail" v-for="v in sellers.infos">
                    <div class="adv_detail_txt  "><p class="seller_detail_txt">{{v}}</p></div>
                </div>
            </div>
        </div>
	</div>

</template>
<script>

    import {mapGetters} from "vuex"
    import Stars from "../stars/stars.vue"
    export default{
        data:function(){
            return {
                status:false,
            }
        },
    	computed:{
    		...mapGetters([
    			"sellers"
    		])
    	},
        created(){
            this.$nextTick(()=>{
                setTimeout(this._initBS,100)
            })
       },
        methods:{
            changeStatus(e){
                if(!e._constructed){
                    return;
                }
                this.status=!this.status;
            },
            _initBS(){
                this.seller = new BScroll(this.$refs.seller,{
                    click : true,
                })
                // 横滚动
                // var imgLength=(this.$refs.picWidth_child.length);
                // console.log(imgLength);
                 
                 this.$refs.picWidth.style.width =500+ 'px';
                 this.view_pics = new BScroll(this.$refs.view_pics,{
                    click : true,
                    scrollX: true
                })
                 // console.log(this.$refs.picWidth.style.width);
            }
        },
        components:{
            Stars
        },
    }
</script>
<style lang="less">
    .all{
        position: absolute;
        top:3.6rem;
        width: 100%;
        bottom:0.92rem;
        overflow: hidden;
        .header{
            height: 3.4rem;
            border: 0.03rem solid #f3f5f7;
            padding: 0 0.4rem;

            .header_top{
                margin-top: 0.4rem;
                height: 1.3rem;
                border-bottom: 0.01rem solid #f3f5f7;
                display: flex;
                .header_top_left{
                    // border: 1px solid red;
                    flex:5;
                    .header_top_left_title{
                        font-size: 0.4rem;
                    }
                    .header_top_left_detail{
                        display: flex;
                        margin-top: 0.2rem;
                        line-height: 0.25rem;
                        font-size: 0.25rem;
                        align-items:center;
                        .header_top_left_stars{
                            flex:4;
                            & img{
                                width: 0.3rem;
                                margin-right: 0rem;
                            }
                        }
                        .header_top_left_num{
                            flex:2;
                        }
                        .header_top_left_sellCount{
                            flex:4;
                        }
                    }
                }
                .header_top_right{
                     // border: 1px solid red;
                     flex:1;
                     text-align:center;
                     color:gray;
                     font-size:0.25rem;
                     .iconfont_like{
                         margin-bottom:0.2rem;
                        .like{
                            color:red; 
                            font-size:0.5rem; 
                        }
                        .unlike{
                            color:lightgray; 
                            font-size:0.5rem; 
                        }
                     }

                }
            }
            .header_bottom{
                display: flex;
                margin-top: 0.4rem;
                &>div{
                    flex:1;
                    text-align: center;
                    height:1rem;
                    border-right: 0.03rem solid #f3f5f7;
                    .header_bottom_info{
                        color:gray;
                        margin-bottom: 0.1rem;
                    }
                    .header_bottom_amount{
                        font-size: 0.6rem;
                        font-family: "华文细黑";
                    }
                }
                .header_bottom_right{
                     border-right: none;
                }
            }
        }
        .adv{
            border: 0.03rem solid #f3f5f7;
            padding:0.3rem;
            .adv_title{
                font-size:0.4rem;
                margin-bottom:0.2rem;
            }
            .adv_content{
                line-height:0.5rem;
                margin:0.2rem;
                color:#EE2C2C;
                font-size:0.25rem;
            }
            .adv_detail{
                height:0.8rem;
                padding:0.2rem 0;
                border-top:0.01rem solid #f3f5f7;
                display:flex;
                color: 	#616161;
                .adv_detail_img{
                    height:100%;
                    flex:1;
                    padding:0.2rem 0 0.1rem 0.2rem;
                    display:table;
                    &>img{
                        width:60%;
                        display: table-cell;
                        vertical-align: middle;
                    }
                }
                .adv_detail_txt{
                    height:100%;
                    flex:8;
                    line-height:0.7rem;
                    display:table;
                    font-size:0.25rem;
                    &>p{
                        display: table-cell;
                        vertical-align: middle;
                    }
                }
            }
        }
        .view{
            border: 0.03rem solid #f3f5f7;
            padding:0.3rem;
            .view_title{
                font-size:0.4rem;
            }
            // 横滚动
            .view_pics{
                    width: 100%;
                    overflow: hidden;
                &>div{
                    display:flex;
                    height:1.9rem;
                    flex-wrap:nowrap;   
                    & img{
                       margin:0.2rem 0.1rem 0;
                       width: 2.5rem;
                       height: 1.8rem;
                    }

                }
                
            }
        }
        .seller_detail_txt{
            line-height: 0.3rem;
        }
        .split{
            width: 100%;
            height:0.5rem;
            border-top: 0.03rem solid #dddddd;
            border-bottom: 0.03rem solid #dddddd;
            background: #f3f5f7;
        }
    }
</style>



