<template>
	<div class="background_fooddetail" ref="all">
		<div>
		<!-- 顶部 -->
			<div class="header">
				<div class="back" @click="close">
					<i class="iconfont icon-shiliangzhinengduixiang4"></i>
				</div>
				<div class="header_img"><img :src="rec_good.image" alt=""></div>
				<div class="header_detail">
					<div>
						<p class="header_detail_title">{{rec_good.name}}</p>
						<p class="header_detail_info">月销售{{rec_good.sellCount}}份&nbsp;&nbsp;好评率{{rec_good.rating}}%</p>
						<div class="header_detail_detail">
							<div class="header_detail_left">
								<p class="header_detail_price">￥{{rec_good.price}}</p>
								<s class="header_detail_oldprice" v-if="rec_good.oldPrice">￥{{rec_good.oldPrice}}</s>	
							</div>
							<div class="pm"><plusminus :food="rec_good" v-if="rec_good.count"></plusminus></div>
							<div class="header_detail_add" @click="addtocar" v-if="!rec_good.count">加入购物车</div>
						</div>
					</div>
				</div>
			</div>
			<div class="split"></div>
		<!-- 商品介绍 -->
			<div class="intro" >
				<p class="intro_title">商品介绍</p>
				<p class="intro_txt" >{{rec_good.info}}</p>
				<p class="intro_txt"  v-if="!rec_good.info">该商品暂无介绍</p>
			</div>
			<div class="split"></div>
		<!-- 商品评价 -->
			<div class="rating_top">
				<p class="rating_title">商品评价</p>
				<div class="button_group_border">
					<div class="button_group">
						<div class="button" :class="flag=='all'?'button_down1':'button_on1'" @click="flag='all'">
							<p><span class="text">全部 </span>{{countRating[0]}}</p>
						</div>
						<div class="button" :class="flag=='good'?'button_down2':'button_on2'" @click="flag='good'">
							<p><span class="text">推荐 </span>{{countRating[1]}}</p>
						</div>
						<div class="button" :class="flag=='bad'?'button_down3':'button_on3'" @click="flag='bad'">
							<p><span class="text">吐槽 </span>{{countRating[2]}}</p>
						</div>
					</div>
				</div>
				<div class="show_full" @click="status=!status">
					<i class="iconfont icon-duihao" :class="status?'on':'off'"></i>
					<p>只查看有内容的评价</p>
				</div>
			</div>
			<div class="rating_detail">
				<div class="rating_detail_child" v-for="(v,k) in show_ratings" v-if="v.text||status">
					<div class="rating_detail_child_left">
						<p class="rating_detail_child_rateTime">{{v.rateTime|rateTime}}</p>
						<div class="rating_detail_child_cont">
							<i class="iconfont " :class="v.rateType?'rateType_unlike icon-down':'rateType_like icon-damuzhion'"></i>
							<p class="rating_detail_child_text">{{v.text}}</p>
						</div>
					</div>
					<div class="rating_detail_child_right">
						<p class="rating_detail_child_username">{{v.username}}</p>
						<div class="rating_detail_child_avatar">
							<img  :src="v.avatar" alt="">
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import {mapGetters} from "Vuex";
	import plusminus from "../plusAndMinus/plusminus.vue"

	export default{
		data(){
			return{
				 // flag控制是所有、好评、差评
                flag:"all",
                // status控制是否显示全部，true代表显示，false隐藏，默认true
                status:true,
			}
		},
		props:["good","good1"],
		components:{
			plusminus
		},
		computed:{
			...mapGetters(["goods"]),
			// 获取商品数据rec_good
			rec_good(){
				return this.goods[this.good].foods[this.good1]
			},

			// 显示全部评价，好评，差评
            show_ratings(){
                // console.log(this.rec_good);
                var show_ratings=[];
                if(this.flag=="good"){
                    this.rec_good.ratings.forEach( function(rating) {
                        if(rating.rateType==0){
                            show_ratings.push(rating)
                        }
                    });
                    // console.log(show_ratings);
                    return show_ratings;
                }
                if(this.flag=="bad"){
                    this.rec_good.ratings.forEach( function(rating) {
                        if(rating.rateType==1){
                            show_ratings.push(rating)
                        }
                    });
                    // console.log("bad");
                    return show_ratings;
                }
                // console.log('all');
                return this.rec_good.ratings;
            },
			// 统计好评数量、差评数量
            countRating(){
                var countRating=[0,0,0];
                // console.log(this.rec_good.ratings);
                 this.rec_good.ratings.forEach(function(rating) {
                    countRating[0]++;
                    if(rating.rateType==0){
                        countRating[1]++;
                    }
                    if(rating.rateType==1){
                        countRating[2]++;
                    }
                });
                return countRating
            }
		},
		created(){
			this.$nextTick(()=>{
               setTimeout(this._initBS,20) 
            })
		},
		methods:{
			_initBS(){
				this.all=new BScroll(this.$refs.all,{
					click:true
                })
			},
			close(){
				this.$emit("close")
			},
			// 加入商品至购物车
			addtocar(){
				if(!this.rec_good.count){
					this.$set(this.rec_good,"count",1);	
				}
			}
		},
		filters:{
			rateTime(data){
				var time= new Date(data);
				var y = time.getFullYear();//年
				var m = time.getMonth() + 1;//月
				var d = time.getDate();//日
				var h = time.getHours();//时
				var mm = time.getMinutes();//分
				var s = time.getSeconds();//秒
				
				return y+"-"+m+"-"+d+" "+h+":"+mm+":"+s
			},
		}
	}
</script>
<style lang="less">
	.background_fooddetail{
		.back{
			z-index: 99999;
			width: 0.5rem;
			height:0.5rem;
			position: absolute;
			margin:0.2rem;
			color:white;
		}
		position: fixed;
		top:0;
		bottom:1rem;
		left:0;
		right: 0;
		background: rgb(255,255,255);
		z-index: 1;
		overflow: hidden;
		.header{
			width: 100%;
			.header_img{
				width: 100%;
				height: 7rem;
				& img{
					height: 100%;
					width: 100%;
				}	
			}
			.header_detail{
				padding:0.5rem;
				.header_detail_title{
					font-size: 0.4rem;
					margin-bottom: 0.2rem;
				}
				.header_detail_info{
					color:gray;
					margin-bottom: 0.4rem;
				}
				.header_detail_detail{
					display: flex;
					justify-content:space-between;
					.header_detail_left{
						display: flex;
						line-height: 0.4rem;
						.header_detail_price{
							color:red;
							font-size: 0.5rem;
							font-weight: bolder;
							margin-right: 0.3rem;
						}
						.header_detail_oldprice{
							flex:1;
							color:gray;
							font-size: 0.3rem;
						}
						
					}
					.header_detail_add{
						border-radius: 2rem;
						background: rgb(0,160,220);
						width: 1.8rem;
						height: 0.6rem;
						text-align: center;
						line-height: 0.6rem;
						color:white;

					}
					.pm{
						width: 1.8rem;
						height: 0.6rem;
						line-height: 0.6rem;
						margin-right: 0.2rem;
					}
				}
				
			}
		}
		.intro{
			padding:0.5rem;
			.intro_title{
				font-size: 0.4rem;
				margin-bottom: 0.2rem;
			}
			.intro_txt{
				color:gray;
				font-size: 0.3rem;
				line-height: 0.6rem;
			}
		}
		.rating_top{
			padding:0.5rem 0.5rem 0 0.5rem;
			border-bottom: 0.03rem solid rgba(77,85,93,0.2);
			.rating_title{
				font-size: 0.4rem;
				margin-bottom: 0.2rem;
			}
			.button_group_border{
					padding: 0.2rem 0;
					border-bottom: 0.01rem solid rgba(77,85,93,0.2);
				.button_group{
					margin: 0.3rem 0;
					display: flex;
					// border-bottom: 0.01rem solid gray;
					.button{
						width:1.5rem;
						height: 0.8rem;	
						color:white;
						text-align: center;
						line-height: 0.8rem;
						margin-right: 0.2rem;
						.text{
							font-size: 0.3rem;
						}
					}
					// 未点击样式和点击样式的切换
                    .button_on1{
                        background: rgb(0,160,220);
                        color:rgb(77,85,93);
                    }
                    .button_down1{
                        background: rgb(0,160,220);
                        color:white;
                        box-shadow: 0.05rem 0.05rem 1rem rgb(0,160,220)-30 inset;
                    }
                    .button_on2{
                        color:rgb(77,85,93);
                        background: rgba(0,160,220,0.5);
                    }
                    .button_down2{
                        background: rgba(0,160,220,0.5);
                        color:white;
                        box-shadow: 0.05rem 0.05rem 1rem rgba(0,160,220,0.5)-30 inset;
                    }
                    .button_on3{
                        color:rgb(77,85,93);
                        background: rgba(77,85,93,0.2);
                    }
                    .button_down3{
                        background: rgba(77,85,93,0.2);
                        color:white;
                        box-shadow: 0.05rem 0.05rem 1rem rgba(77,85,93,0.2)-30 inset;
                    }
				}
			}
			.show_full{
				display: flex;
				line-height: 1.5rem;
				font-size: 0.4rem;
				color:rgba(77,85,93,0.5);
				i{
					color:rgba(77,85,93,0.5);
					flex:0 0 1rem;
					font-size: 0.6rem;
				}
                .on{
                    color:rgba(77,85,93,0.5);
                    flex:0 0 1rem;
                    font-size: 0.6rem;
                }
                .off{
                    color:#66CD00;
                    flex:0 0 1rem;
                    font-size: 0.6rem;
                }
			}
		}
		.rating_detail{
			padding:0.2rem 0.5rem;
			.rating_detail_child{
				border-bottom: rgba(77,85,93,0.2) 0.01rem solid;
				display:flex;
				justify-content:space-between;
				padding: 0.3rem 0;
				.rating_detail_child_left{
					width: 6rem;
					.rating_detail_child_rateTime{
						font-size: 0.3rem;
						color: rgba(77,85,93,0.7);
						line-height: 0.4rem;
					}
					.rating_detail_child_cont{
						display: flex;
						line-height: 0.4rem;
						padding:0.2rem 0;
						// 赞-----------------------------
						.rateType_unlike{
							flex:0 0 0.5rem;
							font-size: 0.35rem;
							color: rgba(77,85,93,0.7);
						}
						.rateType_like{
							flex:0 0 0.5rem;
							font-size: 0.35rem;
							color: rgb(0,160,220);
						}
						.rating_detail_child_text{
							font-size: 0.4rem;
						}
						
					}
				}
				.rating_detail_child_right{
					display: flex;
					&>p{
						font-size: 0.3rem;
						color:rgba(77,85,93,0.7);
						line-height: 0.5rem;
					}
					&>div{
						width: 0.5rem;
						height: 0.5rem;
						margin-left: 0.25rem;
						border-radius: 0.25rem;
						overflow: hidden;
						&>img{
							width: 100%;
						}
					}
				}	
			}
		}
		.split{
            width: 100%;
            height:0.5rem;
            border-top: 0.03rem solid #dddddd;
            border-bottom: 0.03rem solid #dddddd;
            background: #f3f5f7;
        }
	}
</style>


