<template>
    <div ref="all" class="all_ratings">
        <div>
             <!-- 顶部 -->
       		<div class="header">
                <div class="header_left">
                    <p class="header_left_score">{{sellers.serviceScore}}</p>
                    <p class="header_title">综合评分</p>
                    <p class="header_txt">高于周边商家{{sellers.rankRate}}%</p>
                </div>
                <div class="header_right">
                    <div>
                        <p class="header_title">服务态度</p>
                        <div>
                            <stars-rating :score="sellers.serviceScore"></stars-rating>  
                        </div>
                        <p class="header_right_score">{{sellers.serviceScore}}</p>
                    </div>
                    <div>
                        <p class="header_title">商品评分</p>
                        <div>
                             <stars-rating :score="sellers.foodScore"></stars-rating>
                        </div>
                        <p class="header_right_score">{{sellers.foodScore}}</p>
                    </div>
                    <div>
                        <p class="header_title">送达时间</p>
                        <p class="header_txt">44分钟</p>
                    </div>
                </div>
            </div>
            <div class="split"></div>
            <!-- 内容 -->
            <div class="rating_top">
                <div class="button_group_border">
                    <div class="button_group">
                        <div class="button":class="flag=='all'?'button_down1':'button_on1'" @click="flag='all'">
                            <p><span class="text">全部 </span>{{countRating[0]}}</p>
                        </div>
                        <div class="button":class="flag=='good'?'button_down2':'button_on2'"  @click="flag='good'">
                            <p><span class="text">满意 </span>{{countRating[1]}}</p>
                        </div>
                        <div class="button":class="flag=='bad'?'button_down3':'button_on3'"  @click="flag='bad'">
                            <p><span class="text">不满意 </span>{{countRating[2]}}</p>
                        </div>
                    </div>
                </div>
                <div class="show_full" @click="status=!status">
                    <i class="iconfont icon-duihao" :class="status?'on':'off'"></i>
                    <p>只查看有内容的评价</p>
                </div>
            </div>
            <div class="rating_detail" v-for="(v,k) in show_ratings" v-if="v.text||status">
                <div class="rating_detail_left">
                    <div class="rating_detail_left_img" v-if="v">
                        <img :src="v.avatar" alt="">
                    </div>
                </div>
                <div class="rating_detail_right">
                    <div class="rating_detail_right_top">
                        <div>
                            <p class="rating_detail_right_top_title">{{v.username}}</p>
                            <div class="rating_detail_right_top_info">
                                <p>
                                    <stars-rating :score="v.score"></stars-rating>
                                </p>
                                <p class="rating_detail_right_top_txt" v-if="v.deliveryTime">{{v.deliveryTime}}分钟送达</p>
                            </div>
                        </div>
                        <div class="rating_detail_right_top_txt">{{v.rateTime|rateTime}}</div>
                    </div>
                    <div class="rating_detail_right_text">
                        <p>{{v.text}}</p>
                    </div>
                    <div class="rating_detail_right_command">
                    <!-- {{v.rateTime}} -->
                        <div class="rating_detail_right_command_i">

                          <i class="iconfont" :class="v.rateType?'unlike icon-down':'like icon-damuzhion'"></i>
                        </div>
                        <div v-for="(v1,k1) in v.recommend">
                          <span>{{v1}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        show_ratings{{show_ratings}}
    </div>
</template>
<script>
    import {mapGetters} from "Vuex"
    import Stars from "../stars/stars.vue"
    import StarsRating from "../stars/start_rating.vue"

    export default{
        data(){
            return {
                // flag控制是所有、好评、差评
                flag:"all",
                // status控制是否显示全部，true代表显示，false隐藏，默认true
                status:true,
            }
        },
    	computed:{
    		...mapGetters(["ratings","sellers"]),
            rateTime(){
                var rateTime_arr=[];
                for(var i in this.ratings){
                    var rateTime=this.ratings[i].rateTime;
                    var time= new Date(rateTime);
                    var y = time.getFullYear();//年
                    var m = time.getMonth() + 1;//月
                    var d = time.getDate();//日
                    var h = time.getHours();//时
                    var mm = time.getMinutes();//分
                    var s = time.getSeconds();//秒
                    rateTime_arr.push(y+"-"+m+"-"+d+" "+h+":"+mm+":"+s)
                }
                // console.log(rateTime_arr);
                return rateTime_arr
            },
            // 显示全部评价，好评，差评
            show_ratings(){
                // console.log(this.ratings);
                var show_ratings=[];
                if(this.flag=="good"){
                    this.ratings.forEach( function(rating) {
                        if(rating.rateType==0){
                            show_ratings.push(rating)
                        }
                    });
                    return show_ratings;
                }
                if(this.flag=="bad"){
                    this.ratings.forEach( function(rating) {
                        if(rating.rateType==1){
                            show_ratings.push(rating)
                        }
                    });
                    // console.log("bad");
                    return show_ratings;
                }
                // console.log('all');
                return this.ratings;
            },
            // 统计好评数量、差评数量
            countRating(){
                var countRating=[0,0,0];
                 this.ratings.forEach(function(rating) {
                    countRating[0]++;
                    if(rating.rateType==0){
                        countRating[1]++;
                    }
                    if(rating.rateType==1){
                        countRating[2]++;
                    }
                });
                return countRating
            }
    	},
    	created(){
    		this.$store.dispatch("getRatings");
            this.$nextTick(()=>{
                setTimeout(this._initBS, 20)
            });
    	},
        methods:{
            _initBS(){
                this.all=new BScroll(this.$refs.all,{
                    click:true
                })
            },

        },
        components:{
            Stars,
            StarsRating
        },
        filters:{
            rateTime(data){
                var time= new Date(data);
                var y = time.getFullYear();//年
                var m = time.getMonth() + 1;//月
                var d = time.getDate();//日
                var h = time.getHours();//时
                var mm = time.getMinutes();//分
                var s = time.getSeconds();//秒
                
                return y+"-"+m+"-"+d+" "+h+":"+mm+":"+s
            },
        }
    }
</script>
<style lang="less" >
    .all_ratings{
        position: absolute;
        top:3.6rem;
        width: 100%;
        bottom:0.92rem;
        overflow: hidden;
    	.header{
            display: flex;
            border-top:0.01rem solid lightgray;
            padding: 0.5rem 0;
            .header_title{
                font-size:0.35rem;
            }
            .header_txt{
                font-size:0.25rem;
                color:gray;
            }
            .header_left{
                flex:0 0 2.5rem;
                border-right: 0.01rem solid lightgray;
                text-align: center;
                &>p{
                    margin-bottom: 0.2rem;
                }
                .header_left_score{
                    color:orange;
                    font-size: 0.6rem;
                }
            }
            .header_right{
                flex:1;
                padding:0 0.5rem;
                &>div{
                    display: flex;
                    justify-content:space-between;
                    margin-bottom: 0.3rem;
                }
                .header_right_score{
                    color: orange;
                    font-size: 0.3rem;
                }
            }
        }
        .rating_top{
            padding:0 0.5rem 0 0.5rem;
            border-bottom: 0.03rem solid rgba(77,85,93,0.2);
            .button_group_border{
                    padding: 0.2rem 0;
                    border-bottom: 0.01rem solid rgba(77,85,93,0.2);
                .button_group{
                    margin: 0.3rem 0;
                    display: flex;
                    // border-bottom: 0.01rem solid gray;
                    .button{
                        width:1.5rem;
                        height: 0.8rem; 
                        color:white;
                        text-align: center;
                        line-height: 0.8rem;
                        margin-right: 0.2rem;
                        // color:rgb(77,85,93);
                        .text{
                            font-size: 0.3rem;
                        }
                    }
                    // 未点击样式和点击样式的切换
                    .button_on1{
                        background: rgb(0,160,220);
                        color:rgb(77,85,93);
                    }
                    .button_down1{
                        background: rgb(0,160,220);
                        color:white;
                        box-shadow: 0.05rem 0.05rem 1rem rgb(0,160,220)-30 inset;
                    }
                    .button_on2{
                        color:rgb(77,85,93);
                        background: rgba(0,160,220,0.5);
                    }
                    .button_down2{
                        background: rgba(0,160,220,0.5);
                        color:white;
                        box-shadow: 0.05rem 0.05rem 1rem rgba(0,160,220,0.5)-30 inset;
                    }
                    .button_on3{
                        color:rgb(77,85,93);
                        background: rgba(77,85,93,0.2);
                    }
                    .button_down3{
                        background: rgba(77,85,93,0.2);
                        color:white;
                        box-shadow: 0.05rem 0.05rem 1rem rgba(77,85,93,0.2)-30 inset;
                    }
                }
            }
            .show_full{
                display: flex;
                line-height: 1.5rem;
                font-size: 0.4rem;
                color:rgba(77,85,93,0.5);
                .on{
                    color:rgba(77,85,93,0.5);
                    flex:0 0 1rem;
                    font-size: 0.6rem;
                }
                .off{
                    color:#66CD00;
                    flex:0 0 1rem;
                    font-size: 0.6rem;
                }
            }
        }
        .rating_detail{
            display: flex;
            border-bottom: 0.01rem solid #f3f5f7;
            margin: 0 0.4rem;
            .rating_detail_left{
                padding:0.4rem;
                flex:0 0 0.5rem;
                .rating_detail_left_img{
                    width: 0.8rem;
                    height: 0.8rem;
                    border-radius: 0.4rem;
                    overflow: hidden;
                    &>img{
                        width: 100%;
                        height:100%;   
                    }
                }
            }
            .rating_detail_right{
                flex:1;
                padding:0.4rem 0.4rem 0.4rem 0;
                // border:1px solid red;
                .rating_detail_right_top{
                    display: flex;
                    justify-content:space-between;
                    .rating_detail_right_top_title{
                        font-size: 0.3rem;
                        margin-bottom: 0.2rem;
                    }
                    .rating_detail_right_top_txt{
                        color:gray;
                        margin-bottom: 0.2rem;
                    }
                    .rating_detail_right_top_info{
                        display: flex;
                    }
                }
                .rating_detail_right_text{
                    font-size: 0.3rem;
                    line-height: 0.4rem;
                    margin-bottom: 0.2rem;
                }
                .rating_detail_right_command{
                    width: 100%;
                    // border:1px solid gray;
                    height: 0.48rem;
                    line-height: 0.3rem;
                    overflow: hidden;
                    display: flex;
                    flex-wrap:wrap;
                    .rating_detail_right_command_i{
                        flex:0 0 0.5rem;
                        padding:0.08rem;
                        .like{
                            color:#00a0dc;
                        }
                        .unlike{
                            color: gray;
                        }
                    }
                    & span{
                        border: 0.01rem solid lightgray;
                        margin-right: 0.3rem;
                        width: 1rem;
                        white-space: nowrap;
                        text-overflow:ellipsis;
                        overflow: hidden;
                        text-align: center;
                        padding:0.05rem 0.1rem; 
                        display: inline-block;
                        color:gray;
                    }
                }
            }

        }
        .split{
            width: 100%;
            height:0.5rem;
            border-top: 0.03rem solid #dddddd;
            border-bottom: 0.03rem solid #dddddd;
            background: #f3f5f7;
        }
    }
    .stars{
        img{
            width:0.3rem;
        }
    }
</style>



