<template>
	<div class="stars">
	<!-- {{score}} -->
		<div v-for="(v,k) in 5">
			<img src="../../../static/images/star_on.png" v-if="v<=score" >
			<img src="../../../static/images/star_half.png" v-if="v>score&&v<score+1" >
			<img src="../../../static/images/star_off.png" v-if="v>=score+1" >
		</div>
	</div>
</template>

<script>
	export default{
		props:["score"]
	}
</script>

<style lang="less" >
	.stars{
		display: flex;
	}
</style>


