import Vue from 'vue'
import Router from 'vue-router'
import Sellers from "../components/body/sellers.vue"
import Goods from "../components/body/goods.vue"
import Ratings from "../components/body/ratings.vue"

Vue.use(Router)

export default new Router({
  routes: [
      {
        path:"/sellers",
        component:Sellers
      },
      {
        path:"/goods",
        component:Goods
      },
      {
        path:"/ratings",
        component:Ratings
      },
      {
        path:"*",
        redirect:"/goods"
      }
  ],
  linkActiveClass:"active"
})



