<template>
	<div>
		<transition enter-active-class="animated  fadeIn" leave-active-class="animated fadeOut">
			<carlist :car="car" v-if="flag"></carlist>
		</transition>
		<div class="background_car">
		<!-- 图标 -->
			<div class="round_out">
				<div class="round_inner" @click="flag=!flag" :class="totalCount?'on_list':'off_list'">
					<div class="logo">
						<i class="iconfont icon-gouwuche" ></i>
					</div>
				</div>
			</div>
			<transition enter-active-class="animated  rollIn" leave-active-class="animated rollOut">
				<div class="num" v-if='totalCount'>
					{{totalCount}}
				</div>
			</transition>
			<div class="left">
				<div>￥{{calcPrice}}</div>
				<div>另需配送￥{{sellers.deliveryPrice}}元</div>
			</div>
			<div class="right" :class="!totalCount?'on':'off'">
				<span v-if="!totalCount">￥{{sellers.minPrice}}元起送</span>
				<span v-if="totalCount" @click="buy">去结算</span>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapGetters} from "vuex"
	import carlist from "./calllist.vue"
	export default{
		data(){
			return {
				flag:false
			}
		},
		computed:{
			...mapGetters([
				"goods",
				"sellers",

			]),
			// 遍历所有goods，如果有count属性，加入car数组
			car(){
				var foods = [];
				this.goods.forEach( good => {
					good.foods.forEach( food => {
						 if ( food.count > 0){
						 	foods.push(food);
						 }
					})
				})
				return foods;
			},
			// 返回购物车商品总数
			totalCount(){
                var count = 0;
                this.car.forEach(food => {
                    count += food.count;
                });
                return count;
            },
			// 遍历car数组，计算总价
			calcPrice(){
				var total = 0;
				this.car.forEach( food => {
					total += food.price * food.count;
				});
				return total;
			},

		},
		components:{
			carlist
		},
		methods:{
			buy(){
				alert("走，我们去结算！")
			}
		}

	}
</script>

<style lang="less" scoped>
	.background_car{
		padding-left:0.4rem;
		height: 1rem;
		background: #141d27;
		color:rgba(255,255,255,0.6);
		font-size: 0.3rem;
		line-height: 1rem;
		display: flex;
		justify-content:space-between;

		.num{
			width: 0.6rem;
			line-height: 0.45rem;
			height: 0.4rem;
			background: red;
			color:white;
			position: fixed;
			left:1.2rem;
			bottom:0.8rem;
			z-index: 1000;
			text-align: center;
			border-radius: 0.2rem;
		}
		.left{
			// border:1px solid red;
			display: flex;
			justify-content:space-between;
			width: 3rem;
			margin-left: 1.5rem;
		}
		.right{
			// background: rgba(255,255,255,0.2);
			padding:0 0.4rem;
			width: 1.3rem;
			text-align: center;
		}
		.on{
			background: rgba(255,255,255,0.2);
		}
		.off{
			color:white;
			background: #66CD00;
		}

		.round_out{
			position: absolute;
			bottom: 0;
			z-index: 99;
			width: 1.2rem;
			height:1.2rem;
			background: #141d27;
			border-radius: 0.6rem;
			.round_inner{
				border-radius: 0.4rem;
				width: 0.9rem;
				height: 0.9rem;
				margin:0.15rem auto;
				text-align: center;
				line-height: 0.9rem;
				& i{
					font-size: 0.4rem;
				}
			}
			.on_list{
				background: #00a0dc;
				color:white;
			}
			.off_list{
				background: rgba(255,255,255,0.2);
			}
		}
	}
</style>


