import types from "../types.js"
import axios from "axios"

const state = {
	sellers:{},
	sellerdetail:false,
	stars_arr:[]
}
const getters = {
	sellers(state){
		return state.sellers
	},
	sellerdetail(state){
		return state.sellerdetail
	},
	stars_arr(state){
		return state.stars_arr
	}
}
const actions = {
	getSellers({commit}){
		axios.get("/shop/static/data.json").then((resp)=>{
			if(resp.data.errno===0){
				commit(types.GET_SELLERS,resp.data.seller)
				commit(types.GET_STARS,resp.data.seller.score)
			}
		})
	},
	showSellerDetail({commit}){
		commit(types.SHOW_SELLER_DETAIL)
	},
	closeSellerDetail({commit}){
		commit(types.CLOSE_SELLER_DETAIL)
	}
}
const mutations = {
	[types.GET_SELLERS](state,sellers){
		state.sellers=sellers;
	},
	[types.SHOW_SELLER_DETAIL](state){
		state.sellerdetail=true;
	},
	[types.CLOSE_SELLER_DETAIL](state){
		state.sellerdetail=false;
	},
	// 返回星星的方法
	[types.GET_STARS](state,score){
		// console.log("分數：",score)
		var on=parseInt(score);
		var half=score%1;
		var off=Math.ceil(5-score);
		var stars_arr=[];
		for(var i=0;i<on;i++){
			stars_arr.push(1);
		}
		if(half!=0){
			stars_arr.push(0.5);
			off--;
		}
		for(var i=0;i<off;i++){
			stars_arr.push(0);
		}
		state.stars_arr=stars_arr;
		// console.log(state.stars_arr)
	}
}

export default {
	state,
	getters,
	mutations,
	actions
}


