<template>
	<div class="carlist">
		<div class="content">
			<div class="top">
				<p>购物车</p>
				<p class="clearall" @click="clearall">清空</p>
			</div>
			<div class="details" ref="detail">
				<div class="aaa">
					<div class="detail" v-for="(v,k) in car">
						<div>{{v.name}}</div>
						<div class="right">
							<p class="price">￥{{calcPrice[k]}}</p>
							<div class="pm">
								<plusminus :food="v"></plusminus>
							</div>
						</div>
					</div>
					<div v-if="!car.length" class="empty">您的购物车空空如也/(ㄒoㄒ)/~~</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import plusminus from "../plusAndMinus/plusminus.vue"
	import {mapGetters} from "vuex"
	export default{
		props:["car"],
		computed:{
			...mapGetters(["goods"]),
			calcPrice(){
				var price_list = [];
				this.car.forEach( food => {
					price_list.push(food.price * food.count)
				});
				return price_list;
			},
		},
		components:{
			plusminus
		},
		created(){
			this.$nextTick(()=>{
               		setTimeout(this._initBS,100); 
           	})
		},
		methods:{
			_initBS(){
                // 左侧菜单
            	this.detail=new BScroll(this.$refs.detail,{
		                    click:true,
		        })
                // console.log(this.detail);
            },
            clearall(){
            	this.car.forEach( function(car) {
            		car.count=0;
            	});

            }
		}


	}
</script>
<style lang="less" scoped>
	.carlist{
		position: fixed;
		bottom: 1rem;
		top:0;
		width: 100%;
		background: rgba(0,0,0,0.8);
		blur:10px;
		.content{
			position: fixed;
			background: white;
			width: 100%;
			bottom:1rem;
			top:6rem;
			.top{
	            height:0.8rem;
	            border-top: 0.03rem solid #dddddd;
	            border-bottom: 0.03rem solid #dddddd;
	            background: #f3f5f7;
            	display: flex;
            	justify-content:space-between;
            	line-height: 0.8rem;
            	font-size: 0.3rem;
            	padding:0 0.3rem;
            	.clearall{
            		color:#00a0dc;
            	}
        	}
        	.details{
        		position: fixed;
        		overflow: hidden;
        		top:6.8rem;
        		bottom:1rem;
        		width: 100%;
	        	.detail{
					display: flex;
					border-bottom:0.01rem solid #DEDEDE;
					margin: 0 0.3rem;
					padding:0.3rem 0;
					justify-content:space-between;
					font-size: 0.3rem;
					.right{
						width: 3rem;
						display: flex;
						// border:1px solid red;
						.price{
							color:red;
							margin-right:0.5rem; 
							// border:1px solid red;
							width: 0.5rem;
						}
						.pm{
							width: 1.3rem;
							// border:1px solid red;
						}
					}
	        	}
        		
        	}
        	.empty{
        		text-align: center;
        		line-height: 5rem;
        		font-size: 0.3rem;
        		color:lightgray;
        	}
		}
	}
</style>


