<template>
		<div class="stars_1">
			<div v-for="(v,k) in 5">
				<img src="../../../static/images/star_half.png" v-if="stars_arr[k]==0.5" >
				<img src="../../../static/images/star_on.png" v-if="stars_arr[k]==1" >
				<img src="../../../static/images/star_off.png" v-if="stars_arr[k]==0" >
			</div>
		</div>
</template>
<script>
	import {mapGetters} from "Vuex"

	export default{
		computed:{
			...mapGetters(["stars_arr"])
		}
	} 
</script>
<style lang="less" >
	.stars_1{
		width:100%;
		display:flex;
		justify-content:space-between;

	}
	
</style>


